body {
  font-family: "Aileron", sans-serif;
  font-display: swap;
  background-color: #141414;
  height: 100vh;
  overflow-x: hidden;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #FFFFFF;
  overflow: hidden;
}

.loader {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #141414;
}
.loader__img {
  width: 300px;
}

.registration {
  background: url("../components/img/bg.jpg");
  width: 100%;
  height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 150px;
}
.registration__container {
  max-width: 1400px;
  padding: 15px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.registration .registration-form__block {
  display: flex;
  flex-direction: column;
}
.registration .registration-form {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.registration .registration-form__registration-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.registration .registration-form__title {
  font-size: 42px;
  text-align: center;
  margin-bottom: 30px;
}
.registration .registration-form__field {
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
}
.registration .registration-form__button {
  font-weight: 600;
}
.registration .registration-social__gmail-title {
  position: relative;
  padding-left: 30px;
}
.registration .registration-social__gmail-title::after {
  content: "";
  margin-top: -7px;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0;
  left: 0;
  background: url("../components/img/gmail.svg") 25px 25px/cover;
}
.registration .registration-social__telegram-title {
  position: relative;
  padding-left: 30px;
}
.registration .registration-social__telegram-title::after {
  content: "";
  margin-top: -7px;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0;
  left: 0;
  background: url("../components/img/telegram.svg") 25px 25px/cover;
}

.navbar {
  position: fixed;
  z-index: 555;
  background-color: hsla(0, 0%, 8%, 0.2);
  width: 100%;
  transition: 0.3s;
}
.navbar.scrolled {
  transition: 0.3s;
  background-color: hsla(0, 0%, 8%, 0.9);
}
.navbar__container {
  max-width: 1400px;
  padding: 15px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: 0.3s;
}
.navbar__container.scrolled {
  transition: 0.3s;
  padding: 10px;
}
.navbar__list {
  display: flex;
  text-transform: uppercase;
  justify-content: center;
  flex: 1 1 auto;
}
.navbar__list-block {
  display: flex;
  gap: 25px;
}
.navbar__list-elemet {
  display: inline-block;
  color: black;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  border: none;
  background: transparent;
}
.navbar__list-elemet a {
  width: 100px;
  height: auto;
  float: left;
  transition: 0.5s linear;
  position: relative;
  display: block;
  overflow: hidden;
  padding: 10px 15px;
  text-align: center;
  margin: 0 5px;
  background: transparent;
  text-transform: uppercase;
  font-weight: 900;
}
.navbar__list-elemet a:before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  height: 2px;
  width: 100%;
  border-bottom: 2px solid transparent;
  border-left: 2px solid transparent;
  box-sizing: border-box;
  transform: translateX(100%);
}
.navbar__list-elemet a:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  border-top: 2px solid transparent;
  border-right: 2px solid transparent;
  box-sizing: border-box;
  transform: translateX(-100%);
}
.navbar__list-elemet a:hover {
  box-shadow: 0 3px 15px rgba(255, 255, 255, 0.5);
}
.navbar__list-elemet a:hover:before {
  border-color: #ffffff;
  height: 100%;
  transform: translateX(0);
  transition: 0.3s transform linear, 0.3s height linear 0.3s;
}
.navbar__list-elemet a:hover:after {
  border-color: #ffffff;
  height: 100%;
  transform: translateX(0);
  transition: 0.3s transform linear, 0.3s height linear 0.5s;
}
.navbar__social {
  display: flex;
  gap: 20px;
}
.navbar__social a img {
  width: 25px;
  height: 25px;
}
.navbar__logo {
  transition: 0.3s;
  z-index: 1;
}
@media (max-width: 840px) {
  .navbar__logo {
    grid-column: 3/4;
    text-align: right;
  }
}
.navbar__logo-svg {
  width: 70px;
  height: 50px;
}
.navbar__reg-button {
  margin-left: 15px;
  background-color: none;
  text-decoration: none;
  background-color: hsl(0, 0%, 8%);
  border: none;
}
.navbar__reg-button:hover {
  box-shadow: 0 3px 15px rgba(255, 255, 255, 0.5);
  transition: 0.3s 0.5s box-shadow linear;
}
.navbar__reg-button-a {
  position: relative;
  display: inline-block;
  padding: 8px 16px;
  border: 2px solid #fefefe;
  text-transform: uppercase;
  color: #fefefe;
  text-decoration: none;
  font-weight: 600;
  font-size: 15px;
}
.navbar__reg-button-a::before {
  content: "";
  position: absolute;
  top: 6px;
  left: -2px;
  width: calc(100% + 4px);
  height: calc(100% - 12px);
  background-color: hsl(0, 0%, 8%);
  transition: 0.3s ease-in-out;
  transform: scaleY(1);
}
.navbar__reg-button-a::after {
  content: "";
  position: absolute;
  left: 6px;
  top: -2px;
  height: calc(100% + 4px);
  width: calc(100% - 12px);
  background-color: hsl(0, 0%, 8%);
  transition: 0.3s ease-in-out;
  transform: scaleX(1);
  transition-delay: 0.5s;
}
.navbar__reg-button-a:hover::before {
  transform: scaleY(0);
}
.navbar__reg-button-a:hover::after {
  transform: scaleX(0);
}
.navbar__reg-button-span {
  position: relative;
  z-index: 3;
}

