// Import regular font

body {
    font-family: $font-name, sans-serif;
    font-display: swap; // Add any additional parameters here
    background-color: #141414;
    height: 100vh;
    overflow-x: hidden;
}
#root{
    display: flex;
	flex-direction: column;
	min-height: 100%;
	color: #FFFFFF;
	overflow: hidden;
}
.loader{
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #141414;
        &__img{
            width: 300px;
        }
}
.registration{
    background: url("../components/img/bg.jpg");
    width: 100%;
    height: 100vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 150px;
    &__container{ 
        max-width: 1400px;
        padding: 15px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .registration-form__block{
        display: flex;
        flex-direction: column;
    }
    .registration-form{
        display: flex;
        flex-direction: column;
        gap: 30px;
        &__registration-form{
            display: flex;
            flex-direction: column;
            gap: 15px;
        }
        &__title{
            font-size: 42px;
            text-align: center;
            margin-bottom: 30px;
        }
        &__field{
            background-color: #fff;
            border-radius: 10px;
            overflow: hidden;
        }
        &__button{
            font-weight: 600;
        }
    }
    .registration-social{
        &__gmail-title{
            position: relative;
            padding-left: 30px;
            &::after{
                content: '';
                margin-top: -7px;
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("../components/img/gmail.svg") 25px 25px/ cover ;
            }
        }
        &__telegram-title{
            position: relative;
            padding-left: 30px;
            &::after{
                content: '';
                margin-top: -7px;
                width: 25px;
                height: 25px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("../components/img/telegram.svg") 25px 25px/ cover ;
            }
        }
    }
}

.navbar{
    position: fixed;
    z-index: 555;
    background-color: hsl(0, 0%, 8%,0.2);
    width: 100%;
    transition: 0.3s;
    &.scrolled{
        transition: 0.3s;
        background-color: hsl(0, 0%, 8%,0.9);
    }    
    &__container{
        max-width: 1400px;
        padding: 15px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        gap: 15px;
        transition: 0.3s;
        &.scrolled{
            transition: 0.3s;
            padding: 10px;
        }
    }
    &__list{
        display: flex;
        text-transform: uppercase;
        justify-content: center;
        flex: 1 1 auto;
    }
    &__list-block{
        display: flex;
        gap: 25px;
    }
    &__list-elemet{
            display: inline-block;
            color: black;
            text-decoration: none;
            cursor: pointer;
            outline: none;
            border: none;
            background: transparent;
            & a{
                width: 100px;
                height: auto;
                float: left;
                transition: .5s linear;
                position: relative;
                display: block;
                overflow: hidden;
                padding: 10px 15px;
                text-align: center;
                margin: 0 5px;
                background: transparent;
                text-transform: uppercase;
                font-weight: 900;
              &:before {
                position: absolute;
                content: '';
                left: 0;
                bottom: 0;
                height: 2px;
                width: 100%;
                border-bottom: 2px solid transparent;
                border-left: 2px solid transparent;
                box-sizing: border-box;
                transform: translateX(100%);
              }
              &:after {
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 100%;
                height: 2px;
                border-top: 2px solid transparent;
                border-right: 2px solid transparent;
                box-sizing: border-box;
                transform: translateX(-100%);
              }
              
              &:hover {
                box-shadow: 0 3px 15px rgba(255, 255, 255, 0.5);
              }
              
              &:hover:before {
                border-color: #ffffff;
                height: 100%;
                transform: translateX(0);
                transition: .3s transform linear, .3s height linear .3s;
              }
              
              &:hover:after {
                border-color: #ffffff;
                height: 100%;
                transform: translateX(0);
                transition: .3s transform linear, .3s height linear .5s;
              }
            }

    }
    &__social{
        display: flex;
        gap: 20px;
        & a {
            & img{
                width: 25px;
                height: 25px;
            }
        }
    }
    
    &__logo{
        transition:0.3s;
        z-index: 1;
        @media (max-width:840px){
            grid-column: 3/4;
            text-align: right;
        }
    }
    &__logo-svg{
            width: 70px;
            height: 50px;
    }

        
        &__reg-button{
            margin-left: 15px;
            background-color: none;
            text-decoration: none;
            background-color: hsl(0, 0%, 8%,1);
            border: none;
            &:hover{
                box-shadow: 0 3px 15px rgba(255, 255, 255, 0.5);
                transition: .3s 0.5s box-shadow linear;
            }
        }
        &__reg-button-a{
            position: relative;
            display: inline-block;
            padding: 8px 16px;
            border: 2px solid #fefefe;
            text-transform: uppercase;
            color: #fefefe;
            text-decoration: none;
            font-weight: 600;
            font-size: 15px;
            &::before{
                content: '';
                position: absolute;
                top: 6px;
                left: -2px;
                width: calc(100% + 4px);
                height: calc(100% - 12px);
                background-color: hsl(0, 0%, 8%,1);
                transition: 0.3s ease-in-out;
                transform: scaleY(1);
            }
            &::after{
                content: '';
                position: absolute;
                left: 6px;
                top: -2px;
                height: calc(100% + 4px);
                width: calc(100% - 12px);
                background-color: hsl(0, 0%, 8%,1);
                transition: 0.3s ease-in-out;
                transform: scaleX(1);
                transition-delay: 0.5s;
            }
            &:hover::before{
                transform: scaleY(0);
            }
            &:hover::after{
                transform: scaleX(0);
            }
        }
        &__reg-button-span{
            position: relative;
            z-index: 3;
        }
}

