        padding-top: 50px;
    }

}
.service{
    padding-top: 50px;
    &__container{
        max-width: 1400px;
        margin: 0 auto;
        padding: 15px;
        display: grid;
        grid-template-columns: 50% 50%;
        gap: 15px;
        @media (max-width:840px) {
            grid-template-columns: 1fr;
        }
    }
    .service-block{
        &__title{
            font-size: 42px;
            margin-bottom: 60px;
        }
        &__img{
            @media (max-width:840px) {
                display: none;
            }
            & img{
                width:100%;
                max-width: 620px;
                max-height: 300px;
            }
        }
    }
    .service-content{
        padding-top: 10px;
        &__block{
            display: grid;
            grid-template-columns: 40px 1fr;
            gap: 10px;
        }
        &__number{
            font-size: 30px;
            color: #9b9b9b;
        }
        &__title{
            font-size: 26px;
        }
        &__text{
            grid-column: 2/3;
            font-size: 14px;
            margin-bottom: 20px;
            
        }
    }
}
.about{
